<style scoped>
  ul {
    list-style-type:none;
    margin-left: 0;
    padding-left: 16px;
    padding-right: 16px;
    padding-bottom: 5px
  }
  li {
    margin-bottom: 10px;
  }
</style>

<template>
<v-container>
  <v-row>
    <v-col v-if="!isTestEmpty(body)" cols="12">
      <v-card class="mx-auto">
	<v-card-title><strong>Body check</strong></v-card-title>
	<ul>
	  <li>Morfotipo: {{this.body.bodyType}}</li>
	  <li>Indice di massa corporea: {{this.body.bmi}}</li>
	  <li>Metabolismo basale: {{this.body.bmr}}</li>
	  <li>Calorie consigliate: {{this.body.kcal}} kcal</li>
	  <li>Acqua consigliata giornaliera: {{this.body.wid}} litri</li>
	  <li>Peso da raggiungere: {{this.body.ibw}} Kg</li>
	  <li>Massa magra: {{this.body.lbm}} Kg</li>
	  <li v-if="body.bfp">Massa grassa: {{this.body.bfp}}%</li>
	  <li><strong>Integratori:</strong> {{this.bodyOutputComp.join(' + ')}}.</li>
	</ul>
      </v-card>
    </v-col>
    <v-col v-if="!isTestEmpty(detox)" cols="12">
      <v-card class="mx-auto">
	<v-card-title><strong>Detox</strong></v-card-title>
	<ul>
	  <li>Risultato: {{this.detox.resultDetox}}</li>
	  <li v-for="(v, n) in detoxOutputComp" :key="n"><strong>{{n}}</strong>: {{v}}</li>
	</ul>
      </v-card>
    </v-col>
    <v-col v-if="!isTestEmpty(stress)" cols="12">
      <v-card class="mx-auto">
	<v-card-title><strong>Stress</strong></v-card-title>
	<ul>
	  <li>Risultato: {{this.stress.resultStress}}</li>
	  <li v-for="(v, n) in stressOutputComp" :key="n"><strong>{{n}}</strong>: {{v}}</li>
	</ul>
      </v-card>
    </v-col>
    <v-col v-if="!isTestEmpty(sleep)" cols="12">
      <v-card class="mx-auto">
	<v-card-title><strong>Sleep</strong></v-card-title>
	<v-list-item>Risultato epworth: {{this.sleep.resultSleep}} / 24</v-list-item>
	<ul>
	  <li>Risultato: {{this.sleep.resultSleep}}</li>
	  <li v-for="(v, n) in sleepOutputComp" :key="n"><strong>{{n}}</strong>: {{v}}</li>
	</ul>
      </v-card>
    </v-col>    
    <v-col v-if="!isTestEmpty(body)" cols="12">
      <v-card class="mx-auto">
	<v-card-title><strong>Dieta</strong></v-card-title>
	<DietaMela v-if="this.body.bodyType === 'mela'" />
	<DietaPera v-if="this.body.bodyType === 'pera'"/>
	<DietaMelanzana v-if="this.body.bodyType === 'melanzana'"/>
      </v-card>
    </v-col>
    
    <v-col v-if="!isTestEmpty(body)" cols="12">
      <v-card class="mx-auto">
	<v-card-title><strong>Attivit Fisica</strong></v-card-title>
	<ul>
	  <li>{{this.activityOutput()}}</li>
	</ul>
      </v-card>
    </v-col>
    
  </v-row>
</v-container>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import isEmpty from 'lodash/isEmpty';
import DietaMela from './DietaMela.vue';
import DietaPera from './DietaPera.vue';
import DietaMelanzana from './DietaMelanzana.vue';

export default {
    data: () => ({
    }),
    methods: {
	isTestEmpty(test) {
	    return isEmpty(test);
	},
	popSupplement(sup) {
	    let index = this.hasSupplement(sup);
	    if(index > -1) {
		this.$store.commit('popSupplement', index);
		return true;
	    }
	    return false;
	},
	bodyOutput() {
	    let result = [];
	    if(this.body.bodyType === "mela") {
		if(this.popSupplement("detox")) {
		    result.push("detox 1 misurino la mattina");
		}
		if(this.body.bmi > 25) {
		    if(this.popSupplement("slim")) {
			result.push("slim 1cp a colazione e sera");
		    }
		}
		return result;
	    }
	    if(this.body.bodyType === "pera") {
		if(this.popSupplement("detox")) {
		    result.push("detox 1 misurino la mattina");
		}
		if(this.popSupplement("dren")) {
		    result.push("dren 1 misurino al pomeriggio");
		}
		if(this.body.bmi > 25) {
		    if(this.popSupplement("slim")) {
			result.push("slim 1cp a pranzo");
		    }
		}
		return result;
	    }
	    if(this.popSupplement("antiage")) {
		result.push("antiage la mattina e la sera");
	    }
	    if(this.body.bmi > 25) {
		if(this.popSupplement("slim")) {
		    result.push("slim 1cp a colazione e sera");
		}
	    }
	    return result;
	},
	detoxOutput() {
	    let result = {};
	    if(!this.popSupplement("detox")) {
		return result;
	    }
	    if(this.detox.resultDetox === "alto") {
		result["Integratori"] = "avete realizzato pi di 10 punti siete probabilmente carichi di tossine e le fasi detox saranno indispensabili per migliorare subito la vostra salute. Esegui due giorni di biodetox Juice ogni settimana per un mese e poi continua con un giorno a settimana per tre mesi.";
	    } else if(this.detox.resultDetox === "medio") {
		result["Integratori"] = "avete realizzato da 4 a 10 punti la fase detox vi sar di grande aiuto. Esegui un giorno di biodetox Juice ogni settimana per tre mesi.";
	    } else {
		result["Integratori"] = "avete realizzato meno di tre punti probabilmente le vostre tossine sono sotto controllo. Ugualmente il consiglio di eseguire ogni tanto qualche giorno di Biodetox mimadigiuno.";
	    }
	    return result;
	},
	stressOutput() {
	    let result = {};
	    if(this.stress.resultStress === "falso") {
		result["Descrizione"] = "Reagisci bene alle situazioni di stress e non presenti sintomi allarmanti determinati dallo stress da lavoro. Se vuoi che la situazione resti invariata anche in futuro, per, dovrai fare un po di prevenzione. Ci saranno infatti periodi in cui il carico di lavoro aumenter. Esamina quindi ancora una volta i sintomi di stress presentati nel test, in particolare quelli di cui hai gi fatto esperienza, seppur di tanto in tanto. Conoscendo meglio i sintomi dello stress da lavoro potrai individuare immediatamente eventuali cambiamenti nelle tue reazioni e prendere per tempo contromisure adeguate. Facendo pi sport, praticando esercizi di rilassamento, concedendoti pause regolari o organizzando il tuo ufficio in maniera pi efficace, ad esempio, potrai superare pi rapidamente e facilmente le situazioni di stress, prima di essere preso nel circolo vizioso del super-lavoro. La tua condizione  ottimale e dovresti trarne maggiore vantaggio. Un servizio telefonico e il sostegno di una segretaria a distanza possono supportarti nel tuo lavoro quotidiano e migliorare ulteriormente la qualit della tua vita personale e professionale. In quanto esperti del settore saremo lieti di fornirti dei consigli mirati.";
		return result;
	    }
	    if(this.stress.resultStress === "vero") {
		result["Descrizione"] = "Probabilmente lesito di questo test non ti sorprender: nella tua vita sono gi presenti sintomi di stress da lavoro rilevanti, che non dovresti pi n ignorare n sottovalutare. La pressione alla quale sei sottoposto ha di gran lunga superato il limite che separa uno stile di vita sano da uno che non lo . Forse il fatto di convivere con alti livelli di stress per un periodo di tempo prolungato ti fa percepire alcuni sintomi nominati nel test come assolutamente normali. Se pensi che questo sia un segno (positivo) del fatto che sei riuscito a scendere a patti con lo stress, ti sbagli. Considerare normale la situazione in cui ti trovi  infatti un ulteriore sintomo di uno stile di vita estenuante che dovresti al pi presto abbandonare. Sicuramente un tempo la tua vita era diversa, anche se fai fatica a ricordartene.  quindi giunto il momento di voltare pagina. Ed ecco la buona notizia: per migliorare la tua condizione non hai assolutamente bisogno di un tempo infinito, n di fare sforzi sovrumani. Tutto quello che ti serve  una maggiore disciplina: introdurre dei piccoli cambiamenti alla volta, e attenerti ad essi con costanza. Per iniziare, ad esempio, bastano 15 minuti al giorno da dedicare ad esercizi di rilassamento. Ma  importante non sgarrare! Prova anche con gli esercizi di meditazione o con il training autogeno: ti renderai conto di esserti completamente dimenticato di come ci si sente ad essere veramente rilassati! Una volta che questi 15 minuti saranno diventati una parte integrante delle tue giornate quotidiane, potrai pensare ai prossimi passi da compiere: fare pi esercizio fisico, prenderti delle pause regolari dal lavoro, imparare a delegare le attivit secondarie... Il nostro servizio di segretariato pu aiutarti in questo processo di cambiamento e di ridefinizione delle tue priorit. Delegando le attivit secondarie alle nostre segretarie potrai ridurre significativamente lo stress causato dal carico di lavoro quotidiano e avrai pi tempo da dedicare al miglioramento della tua vita, professionale e privata.";
	    } else if(this.stress.resultStress === "quasi vero") {
		result["Descrizione"] = "Nella tua vita quotidiana sono gi presenti reazioni fisiche e mentali determinate dallo stress da lavoro. Apparentemente ti trovi in una situazione lavorativa molto stressante, le cui richieste ti sembrano superiori alle tue capacit di fronteggiarle. Tieni presente che ogni domanda alla quale hai risposto con vero o parzialmente vero porta con s il rischio di un peggioramento di questa condizione. Dovresti quindi iniziare a prendere delle contromisure adeguate per tutelare il tuo equilibrio psicofisico. Ridurre il livello di stress potrebbe sembrarti un compito impossibile, altrimenti  ti dirai  lo avresti gi fatto. Tuttavia, anche nella tua situazione  possibile adottare dei piccoli cambiamenti che miglioreranno significativamente il modo in cui vivi le tue giornate, e che di conseguenza ti faranno ottenere anche risultati migliori sul lavoro. Solo apportando dei cambiamenti al tuo stile di vita potrai interrompere il circolo vizioso che ti tiene sospeso tra un lavoro snervante e linsoddisfazione per dei risultati poco soddisfacenti. Particolarmente utili a questo scopo sono 15 minuti di esercizio fisico al giorno (preferibilmente allaria aperta), 10 minuti di esercizi di respirazione e di meditazione (che al giorno doggi possono essere praticati anche indipendentemente da ogni forma di pensiero esoterico) e laccantonamento di tutte le attivit secondarie o non importanti.";
	    } else if(this.stress.resultStress === "nessuna maggioranza") {
		result["Descrizione"] = "Nella tua vita quotidiana sono gi presenti reazioni fisiche e mentali determinate dallo stress da lavoro. Apparentemente ti trovi in una situazione lavorativa molto stressante, le cui richieste ti sembrano superiori alle tue capacit di fronteggiarle. Tieni presente che ogni domanda alla quale hai risposto con vero o parzialmente vero porta con s il rischio di un peggioramento di questa condizione. Dovresti quindi iniziare a prendere delle contromisure adeguate per tutelare il tuo equilibrio psicofisico. Ridurre il livello di stress potrebbe sembrarti un compito impossibile, altrimenti  ti dirai  lo avresti gi fatto. Tuttavia, anche nella tua situazione  possibile adottare dei piccoli cambiamenti che miglioreranno significativamente il modo in cui vivi le tue giornate, e che di conseguenza ti faranno ottenere anche risultati migliori sul lavoro. Solo apportando dei cambiamenti al tuo stile di vita potrai interrompere il circolo vizioso che ti tiene sospeso tra un lavoro snervante e linsoddisfazione per dei risultati poco soddisfacenti. Particolarmente utili a questo scopo sono 15 minuti di esercizio fisico al giorno (preferibilmente allaria aperta), 10 minuti di esercizi di respirazione e di meditazione (che al giorno doggi possono essere praticati anche indipendentemente da ogni forma di pensiero esoterico) e laccantonamento di tutte le attivit secondarie o non importanti.";
	    }
	    let tmpSup = [];
	    if(this.popSupplement("stress relieve")) {
		tmpSup.push("stress relieve");
	    }
	    if(this.popSupplement("antiage")) {
		tmpSup.push("antiage");
	    }
	    if(tmpSup.length > 0) {
		result["Integratori"] = tmpSup.join(' + ');
	    }
	    result["Esercizi"] = "meditazione";
	    result["Trattamenti"] = "Antistress";
	    return result;
	},
	sleepOutput() {
	    let result = {};
	    if(this.sleep.resultSleep <= 10) {
		result["Livello"] = "basso";
		result["Descrizione"] = "La sonnolenza  normale non ci sono indicazioni a procedere verso ulteriori accertamenti.";
	    } else if(this.sleep.resultSleep <= 17) {
		result["Livello"] = "medio";
		result["Descrizione"] = "La sonnolenza  abbastanza marcata. Utile eseguire monitoraggio del sonno.";
		result["Strategie"] = "Terapia posizionale nelle apnee ostruttive del sonno";
		if(this.popSupplement("sleep better")) {
		    result["Integratori"] = "sleep better";
		}
	    } else {
		result["Livello"] = "alto";
		result["Descrizione"] = "La sonnolenza  marcata. Utile eseguire una valutazione strumentale del sonno e visita presso il centro del sonno di riferimento. Come strategie si pu eseguire.";
		result["Strategie"] = "Terapia posizionale nelle apnee ostruttive del sonno.";
		if(this.popSupplement("sleep better")) {
		    result["Integratori"] = "sleep better";
		}
	    }
	    return result;
	},
	activityOutput() {
	    if(this.body.bodyType === "mela") {
		return "Se lobiettivo  la perdita di peso,  preferibile svolgere attivit fisica al mattino a digiuno dopo avere bevuto due bicchieri di acqua tiepida con aceto di mele; se si vuole tonificare i muscoli, meglio svolgere la seduta alla sera.";
	    }
	    if(this.body.bodyType === "pera") {
		return " consigliata al mattino a digiuno dopo avere bevuto due bicchieri di acqua tiepida con aceto di mele. Lattivit dovr essere aerobica senza sovraccaricare le gambe, quindi andr bene la cosiddetta ginnastica ellittica (che coinvolge contemporaneamente arti inferiori e superiori) e la cyclette orizzontale. Utile anche la tonificazione e i pesi soprattutto della parte superiore del corpo.";
	    }
	    return "Dovr allenare ogni aspetto fisico, tonificando e incre- mentando la forza per stimolare sia gli ormoni sessuali sia il GH. Occorre dunque eseguire attivit aerobica a media intensit ma, prima di tutto, sar necessario ristabilire il giusto equilibrio mente-corpo e prevedere allinterno della giornata i momenti adatti per prendersi cura di s.";
	},
    },
    computed: {
	    ...mapState(['body', 'detox', 'stress', 'sleep']),
	    ...mapGetters(['getSubmitData', 'hasSupplement']),
	bodyOutputComp() {
	    return this.bodyOutput();
	},
	detoxOutputComp() {
	    return this.detoxOutput();
	},
	stressOutputComp() {
	    return this.stressOutput();
	},
	sleepOutputComp() {
	    return this.sleepOutput();
	},
    },
    created() {
	this.$store.commit('setSupplements', ["detox", "slim", "dren", "antiage", "collagene", "stress relieve", "sleep better"]);
    },
    components: {
	DietaMela,
	DietaPera,
	DietaMelanzana,
    },
}  
</script>
